import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { IAttendancePeriod } from "app/system-setup/attendance-setup/models/attendance-period";
import { AttendancePeriodService } from "app/system-setup/attendance-setup/services/attendance-period.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidators } from "ngx-custom-validators";

@Component({
  selector: "app-attendance-period",
  templateUrl: "./attendance-period.component.html",
  styleUrls: ["./attendance-period.component.scss"],
})
export class AttendancePeriodComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;
  constructor(private periodSetupServ: AttendancePeriodService) {
    super();
  }

  ngOnInit(): void {
    // if(this.CheckNoBusinessOrCompany())
    // this.getObject()

    this.form = this.fb.group({
      id: [0, { nonNallable: true }],
      startDay: [
        null,
        [Validators.required, CustomValidators.lte(31), CustomValidators.gt(0)],
      ],
      startMonth: [
        null,
        [Validators.required, CustomValidators.lte(12), CustomValidators.gt(0)],
      ],
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getObject();
  }

  get formCtrls() {
    return this.form.controls;
  }

  create(model: IAttendancePeriod) {
    this.showLoading();
    this.periodSetupServ
      .create(model)
      .subscribe((res: IResponse<IAttendancePeriod>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res.message.messageTypeName);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  submit() {
    if (this.form.valid) {
      const model: IAttendancePeriod = {
        month_setup: {
          id: this.form.value.id,
          startDay: this.form.value.startDay,
          startMonth: this.form.value.startMonth,
        },
      };
      model.month_setup.id == 0 ? this.create(model) : this.update(model);
    }
  }
  update(model: IAttendancePeriod) {
    this.showLoading();
    this.periodSetupServ
      .update(model)
      .subscribe((res: IResponse<IAttendancePeriod>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.updateSuccessfullyMsg(res.message.messageTypeName);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  getObject() {
    this.showLoading();
    this.periodSetupServ
      .getObject()
      .subscribe((res: IResponse<IAttendancePeriod>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.form.patchValue(res.data.month_setup);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }
}
