import {planDetailsToMap} from "app/system-setup/attendance-setup/mapper/attendance-plan";
import {Component, Input, OnInit} from "@angular/core";
import {Params} from "@angular/router";
import {IAttPlanShiftList} from "app/system-setup/attendance-setup/models/plan";
import {AttendancePlanService} from "app/system-setup/attendance-setup/services/attendance-plan.service";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {IResponse} from "app/shared/interfaces/responces";
import {AppHelpers} from "app/shared/resources/app-helpers";
import {Subject} from "rxjs";
import {takeUntil} from "rxjs/operators";

@Component({
  selector: "app-plan-details",
  templateUrl: "./plan-details.component.html",
  styleUrls: ["./plan-details.component.scss"],
})
export class PlanDetailsComponent extends AppHelpers implements OnInit {
  @Input() planId: number;
  groupedPlans = {};

  mandatoryColomns: string[] = ["date", "dayName", "is_Basic", "is_WeekEnd"];
  isLoaded: boolean = false;
  optionalColomns: string[] = [];
  private destroy$ = new Subject<void>();

  constructor(private planServ: AttendancePlanService) {
    super();
  }

  ngOnInit(): void {
    this.activatedRoute.params.subscribe((params: Params) => {
      this.planId = params.planId;
    });
    this.getPlansDetailsById(1);
  }

  getPlansDetailsById(page: number, pageSize?: number) {
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    this.showLoading();
    this.planServ
      .getDetailsById(this.planId)
      .pipe(takeUntil(this.destroy$))
      .subscribe((res: IResponse<IAttPlanShiftList[]>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.groupedPlans = this.groupPlansByIterationNumber(
            planDetailsToMap(res.data)
          );
        } else {
          this.errorList(res.message.messages);
        }
        this.hideLoading();
      });
  }

  iterationNumberAscending(a, b): number {
    return a.key - b.key;
  }

  groupPlansByIterationNumber(plans: IAttPlanShiftList[]) {
    let grouped = {};
    for (let plan of plans) {
      let iterationNumber = plan.iterationNumber;
      if (!grouped[iterationNumber]) {
        grouped[iterationNumber] = [];
      }
      grouped[iterationNumber].push(plan);
    }
    return grouped;
  }
  ngOnDestroy() {
    // Unsubscribe from all observables to prevent memory leaks
    this.destroy$.next();
    this.destroy$.complete();
  }
}
