import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { IShiftsModel } from "../models/shifts";

export function shiftToMap(shifts: IShiftsModel[]): IShiftsModel[] {
  return shifts.map((shift) => {
    return <IShiftsModel>{
      ...shift,
      start_Time: HelpersFunctions.mapTime(shift.start_Time),
      end_Time: HelpersFunctions.mapTime(shift.end_Time),
      start_Time_Lower_Limit: HelpersFunctions.mapTime(
        shift.start_Time_Lower_Limit
      ),
      start_Time_Upper_Limit: HelpersFunctions.mapTime(
        shift.start_Time_Upper_Limit
      ),
      end_Time_Lower_Limit: HelpersFunctions.mapTime(
        shift.end_Time_Lower_Limit
      ),
      end_Time_Upper_Limit: HelpersFunctions.mapTime(
        shift.end_Time_Upper_Limit
      ),
      calc_Departure_From: HelpersFunctions.mapTime(shift.calc_Departure_From),
      calc_Delay_From: HelpersFunctions.mapTime(shift.calc_Delay_From),
    };
  });
}
