import { Component, OnInit } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { attendancePlanToMap } from "app/system-setup/attendance-setup/mapper/attendance-plan";
import { IAttendancePlanModel } from "app/system-setup/attendance-setup/models/plan";
import { AttendancePlanService } from "app/system-setup/attendance-setup/services/attendance-plan.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-plan-index",
  templateUrl: "./plan-index.component.html",
  styleUrls: ["./plan-index.component.scss"],
})
export class PlanIndexComponent extends AppHelpers implements OnInit {
  submitted: boolean = false;
  mandatoryColomns: string[] = ["Ser", "id", "ar_Name", "en_Name", "actions"];
  optionalColomns: string[] = [
    "start_Date",
    "end_Date",
    "group_Days",
    "description",
  ];
  dataSource: MatTableDataSource<IAttendancePlanModel>;
  isLoaded: boolean = false;

  // private destroy$ = new Subject<void>();

  constructor(public planService: AttendancePlanService) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany())
      this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
  }

  getList(page: number, pageSize?: number) {
    this.isLoaded = false;
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    this.removePageParams();
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.planService
      .getList(params)
      // .pipe(takeUntil(this.destroy$))
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IAttendancePlanModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          if (res.data.length == 0 && page > 1) {
            this.getList(this.prevPage);
            return;
          }
          this.dataSource = new MatTableDataSource(
            attendancePlanToMap(res.data)
          );
          this.isLoaded = true;
          this.page.length = res.meta.totalItemCount;
        }
        else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  onDelete(plan: IAttendancePlanModel): void {
    let msg = this.msgTranslate(plan.ar_Name, plan.en_Name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(plan);
    });
  }

  delete(plan: IAttendancePlanModel) {
    this.showLoading();
    this.planService.Delete(plan.id).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        if (this.dataSource.data.length == 1 && this.currentPage > 1) {
          this.getList(this.prevPage, this.page.pageSize);
        }
        else {
          // if not last item in list: get current page
          this.getList(this.currentPage, this.page.pageSize);
        }
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
      }
      else this.errorList(res.message.messages);
      this.hideLoading();
    });
  }

  // ngOnDestroy() {
  //   // Unsubscribe from all observables to prevent memory leaks
  //   this.destroy$.next();
  //   this.destroy$.complete();
  // }
}
