import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { IAttPlanShiftList, IAttendancePlanModel } from "../models/plan";

export function attendancePlanToMap(
  attendancePlans: IAttendancePlanModel[]
): IAttendancePlanModel[] {
  return attendancePlans.map((plan) => {
    return <IAttendancePlanModel>{
      ...plan,
      start_Date: HelpersFunctions.mapDate(plan.start_Date),
      end_Date: HelpersFunctions.mapDate(plan.end_Date),
    };
  });
}

export function planDetailsToMap(
  planDetails: IAttPlanShiftList[]
): IAttPlanShiftList[] {
  return planDetails.map((day) => {
    return <IAttPlanShiftList>{
      ...day,
      dayName: HelpersFunctions.getDayName(new Date(day.date)),
      date: HelpersFunctions.mapDate(day.date),
    };
  });
}
