import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { IOfficialHolidaysModel, IOfficialHolidaysRequest, } from "app/system-setup/attendance-setup/models/official-holidays";
import { OfficialHolidaysService } from "app/system-setup/attendance-setup/services/official-holidays.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
  selector: "app-official-holidays",
  templateUrl: "./official-holidays.component.html",
  styleUrls: ["./official-holidays.component.scss"],
})
export class OfficialHolidaysComponent extends AppHelpers implements OnInit {
  dataSource: MatTableDataSource<IOfficialHolidaysModel>;
  form: FormGroup;
  submitted: boolean = false;
  mandatoryColomns: string[] = ["Ser", "id", "en_Name", "ar_Name", "actions"];
  isLoaded: boolean = false;
  optionalColomns: string[] = [];

  // private destroy$ = new Subject<void>();

  constructor(public holidayService: OfficialHolidaysService) {
    super();
  }

  ngOnInit(): void {
    // if (this.businessId > 0 && this.companyId > 0)
    //   this.getList(this.currentPage);

    this.form = this.fb.group({
      id: [0, {nonNullable: true}],
      ar_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      en_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) this.getList(this.firstPage);
  }

  getList(page: number, pageSize?: number) {
    this.isLoaded = false;
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.holidayService
      .getList(params)
      // .pipe(takeUntil(this.destroy$))
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IOfficialHolidaysModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
          this.page.length = res.meta.totalItemCount;
        }
        else this.errorList(res.message.messages);
      });
  }

  submit() {
    this.submitted = true;
    if (this.form.valid) {
      const model: IOfficialHolidaysRequest = {
        ID: this.form.value.id,
        Ar_Name: this.form.value.ar_Name,
        En_Name: this.form.value.en_Name,
      };
      model.ID == 0 ? this.create(model) : this.update(model);
    }
  }

  create(model: IOfficialHolidaysRequest) {
    this.showLoading();
    this.holidayService
      .create(model)
      .subscribe((res: IResponse<IOfficialHolidaysModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.getList(this.lastPage, this.page.pageSize);
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
        }
        else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  update(model: IOfficialHolidaysRequest) {
    this.showLoading();
    this.holidayService
      .update(model)
      .subscribe((res: IResponse<IOfficialHolidaysModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.getList(this.currentPage, this.page.pageSize);
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
        }
        else {
          this.errorList(res?.message?.messages);
        }
        this.hideLoading();
      });
  }

  holidayToEdit(holiday: IOfficialHolidaysModel) {
    this.form.patchValue(holiday);
  }

  onDelete(holiday: IOfficialHolidaysModel) {
    let msg = this.msgTranslate(holiday.ar_Name, holiday.en_Name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) {
        this.delete(holiday);
      }
    });
  }

  navigateToCalendar(holiday: IOfficialHolidaysModel) {
    this.router.navigate([
      `attendance-setup/${this.moduleId}/holidays-calendar/${holiday.id}`,
    ]);
  }

  delete(model: IOfficialHolidaysModel) {
    this.showLoading();
    this.holidayService.Delete(model.id).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);

        if (this.dataSource.data.length == 1 && this.currentPage > 1) {
          this.getList(this.prevPage, this.page.pageSize);
        }
        else {
          this.getList(this.currentPage, this.page.pageSize);
        }
        if (model.id == this.formCtrls.id.value) this.clearForm();
      }
      else {
        this.errorList(res?.message?.messages);
      }
    });
  }

  clearForm() {
    this.form.reset({id: 0});
    this.submitted = false;
  }

  // ngOnDestroy() {
  //   // Unsubscribe from all observables to prevent memory leaks
  //   this.destroy$.next();
  //   this.destroy$.complete();
  // }
}
